export default "/* eslint-disable class-methods-use-this */\n\nimport React from 'react';\nimport { Form, Field } from 'react-final-form';\nimport DatePicker from 'terra-date-picker';\nimport TerraField from 'terra-form-field';\nimport Button from 'terra-button';\nimport Spacer from 'terra-spacer';\n\nconst validateDate = (value) => {\n  if (!value) {\n    return 'Required';\n  }\n  if (value.search(/[0-1][0-9]\\/([0-2][0-9]|3[0-1])\\/[0-9]{4}/i) <= -1) {\n    return 'Date is Invalid';\n  }\n\n  return undefined;\n};\n\nexport default class MainEntry extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {};\n    this.submitForm = this.submitForm.bind(this);\n  }\n\n  submitForm(values) {\n    this.setState({\n      submittedValues: values,\n    });\n  }\n\n  renderForm({ handleSubmit }) {\n    return (\n      <form\n        noValidate\n        onSubmit={handleSubmit}\n      >\n        <Field\n          name=\"user_date\"\n          validate={validateDate}\n        >\n          {({ input, meta }) => (\n            <TerraField\n              label=\"Enter your planned start date\"\n              error={meta.error}\n              isInvalid={meta.submitFailed && meta.error !== undefined}\n              required\n            >\n              <DatePicker\n                name=\"user_date\"\n                id=\"default\"\n                onChangeRaw={(event, date) => { input.onChange(date); }}\n              />\n            </TerraField>\n          )}\n        </Field>\n        <Button text=\"Submit\" type={Button.Opts.Types.SUBMIT} />\n      </form>\n    );\n  }\n\n  render() {\n    return (\n      <Spacer marginBottom=\"small\">\n        <Form\n          onSubmit={this.submitForm}\n          render={this.renderForm}\n        />\n        {this.state.submittedValues\n          && (\n          <div>\n            <p>Form Submitted Successfully With</p>\n            <pre>{JSON.stringify(this.state.submittedValues, 0, 2)}</pre>\n          </div>\n          )}\n      </Spacer>\n    );\n  }\n}\n"