export default "/* eslint-disable class-methods-use-this */\n\nimport React from 'react';\nimport { Form, Field } from 'react-final-form';\nimport Radio from 'terra-form-radio';\nimport RadioField from 'terra-form-radio/lib/RadioField';\nimport Button from 'terra-button';\nimport Spacer from 'terra-spacer';\n\nconst required = value => (value && value.length > 0 ? undefined : 'Required');\n\nexport default class MainEntry extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {};\n    this.submitForm = this.submitForm.bind(this);\n  }\n\n  submitForm(values) {\n    this.setState({\n      submittedValues: values,\n    });\n  }\n\n  renderForm({ handleSubmit, errors, submitFailed }) {\n    return (\n      <form\n        noValidate\n        onSubmit={handleSubmit}\n      >\n        <RadioField\n          legend=\"Which kind of meal would you like?\"\n          isInvalid={submitFailed && errors.meal !== undefined}\n          error={errors.meal}\n          required\n        >\n          <Field\n            name=\"meal\"\n            type=\"radio\"\n            value=\"chicken\"\n            validate={required}\n            component={({ input }) => (\n              <Radio\n                inputAttrs={{ ...input }}\n                labelText=\"Chicken\"\n                id={input.id}\n                name={input.name}\n                checked={!!input.checked}\n                onChange={input.onChange}\n                value={input.value}\n              />\n            )}\n          />\n          <Field\n            name=\"meal\"\n            type=\"radio\"\n            value=\"vegetarian\"\n            validate={required}\n            component={({ input }) => (\n              <Radio\n                inputAttrs={{ ...input }}\n                labelText=\"Vegetarian\"\n                id={input.id}\n                name={input.name}\n                checked={!!input.checked}\n                onChange={input.onChange}\n                value={input.value}\n              />\n            )}\n          />\n          <Field\n            name=\"meal\"\n            type=\"radio\"\n            value=\"fish\"\n            validate={required}\n            component={({ input }) => (\n              <Radio\n                inputAttrs={{ ...input }}\n                labelText=\"Fish\"\n                id={input.id}\n                name={input.name}\n                checked={!!input.checked}\n                onChange={input.onChange}\n                value={input.value}\n              />\n            )}\n          />\n        </RadioField>\n        <Button text=\"Submit\" type={Button.Opts.Types.SUBMIT} />\n      </form>\n    );\n  }\n\n  render() {\n    return (\n      <Spacer marginBottom=\"small\">\n        <Form\n          onSubmit={this.submitForm}\n          render={this.renderForm}\n        />\n        {this.state.submittedValues\n          && (\n          <div>\n            <p>Form Submitted Successfully With</p>\n            <pre>{JSON.stringify(this.state.submittedValues, 0, 2)}</pre>\n          </div>\n          )}\n      </Spacer>\n    );\n  }\n}\n"